<template>
	<view class="details">
		{{form.id}}<br />
		{{form.ask_type}}<br />
		{{form.students_name}}<br />
		{{form.start_time}}<br />
		{{form.end_time}}<br />
		<button type="default" @tap="draft_delete()">撤销请假</button>
		<button type="default" @tap="extend_leave()">续假</button>
	</view>
</template>

<script>
	export default{
		data(){
			return {
				form:{},
				time:'2024-02-21 00:01:00'
			}
		},
		onLoad: function (option) {
			this.draft_get(option.id)
		},
		methods:{
			// 获取详情
			draft_get(id){
				this.$store.dispatch('ask/draft_get', { id: id }).then(res => {
					this.$data.form = res.data
				});
			},
			// 续假
			extend_leave(){
				this.$store.dispatch('ask/draft_put', { id: this.form.id,extra_time:1,time_back:this.time }).then(res => {
					if(res.code!=2000)return true;
					uni.redirectTo({
						url:'/pages/ask/result?flg=2'
					})
				}).catch(err=>{
					console.log(err.data)
				});
			},
			// 撤销请假条
			draft_delete(){
				this.$store.dispatch('ask/draft_delete', { id: this.form.id }).then(res => {
					uni.redirectTo({
						url:'/pages/ask/result?flg=1'
					})
				})
			}
				
		}
	}
</script>

